
@import '../../../public/assets/_variables';
.cv-home {
    padding: 34px;
    .cv-row {
        display: flex;
        justify-content: space-between;
        .cv-alert {
            color: $color-red !important;
        }
        .cv-warning {
            color: $color-yellow !important;
        }
        .cv-primary {
            color: $color-accent-blue !important;
        }
        .cv-success {
            color: $color-green !important;
        }
        .cv-card {
            width: 100%;
            flex: 1;
            border-radius: 4px;
            padding: 24px;
            box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.06);
            background-color: $color-white !important;
            display: flex;
            flex-direction: column;
            .cv-chart-container {
                width: 100%;
                height: 70px;
            }
            .cv-last-updated {
                display: none; //undo in future
                font-size: 12px;
                text-align: right;
            }
            .cv-label {
                font-weight: 600;
                font-size: 24px;
            }
            .cv-count {
                font-size: 32px;
                font-weight: 600;
                .cv-delta {
                    font-size: 16px;
                    font-weight: 300;
                    float: right;
                }
            }
            .cv-icon {
                margin-right: 8px;
            }

            &:not(:first-child) {
                margin-left: 24px;
            }

            opacity: 0;
            animation: fade-in 300ms ease forwards;
          
            @for $x from 1 through 4 {
              &:nth-child(#{$x}) {
                animation-delay: 200ms * ($x - 1);
              }
            }
        }
        @media screen and (max-width:$table-width) {
            flex-direction: column;
            .cv-card{
                padding: 8px;

                .cv-label {
                    font-size: large;
                }
                .cv-count {
                    font-size: medium;
                    .cv-delta {
                        font-size: medium;
                    }
                }
                .cv-chart-container {
                    height: 60px;
                }
                
                &:not(:first-child) {
                    margin-top: 16px;
                    margin-left: 0;
                }
            }
           
        }
    }
   
    @media screen and (max-width:$table-width) {
        padding: 12px;
    }
}

@keyframes fade-in {
    to {
      opacity: 1;
    }
  }