@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Poppins', sans-serif}body{background-color:#E5E5E5}


.cv-toolbar{width:100%;padding:17px 24px;height:60px;background-color:#fff;border-bottom:2px solid #cccccc}.cv-toolbar .cv-brand{font-size:18px;font-weight:600;color:#109CF1}@media screen and (max-width: 732px){.cv-toolbar .cv-brand{margin-left:0;font-size:24px}}

.cv-home{padding:34px}.cv-home .cv-row{display:flex;justify-content:space-between}.cv-home .cv-row .cv-alert{color:#F7685B !important}.cv-home .cv-row .cv-warning{color:#FFB946 !important}.cv-home .cv-row .cv-primary{color:#109CF1 !important}.cv-home .cv-row .cv-success{color:#2ED47A !important}.cv-home .cv-row .cv-card{width:100%;flex:1 1;border-radius:4px;padding:24px;box-shadow:0px 6px 18px rgba(0,0,0,0.06);background-color:#fff !important;display:flex;flex-direction:column;opacity:0;-webkit-animation:fade-in 300ms ease forwards;animation:fade-in 300ms ease forwards}.cv-home .cv-row .cv-card .cv-chart-container{width:100%;height:70px}.cv-home .cv-row .cv-card .cv-last-updated{display:none;font-size:12px;text-align:right}.cv-home .cv-row .cv-card .cv-label{font-weight:600;font-size:24px}.cv-home .cv-row .cv-card .cv-count{font-size:32px;font-weight:600}.cv-home .cv-row .cv-card .cv-count .cv-delta{font-size:16px;font-weight:300;float:right}.cv-home .cv-row .cv-card .cv-icon{margin-right:8px}.cv-home .cv-row .cv-card:not(:first-child){margin-left:24px}.cv-home .cv-row .cv-card:nth-child(1){-webkit-animation-delay:0ms;animation-delay:0ms}.cv-home .cv-row .cv-card:nth-child(2){-webkit-animation-delay:200ms;animation-delay:200ms}.cv-home .cv-row .cv-card:nth-child(3){-webkit-animation-delay:400ms;animation-delay:400ms}.cv-home .cv-row .cv-card:nth-child(4){-webkit-animation-delay:600ms;animation-delay:600ms}@media screen and (max-width: 732px){.cv-home .cv-row{flex-direction:column}.cv-home .cv-row .cv-card{padding:8px}.cv-home .cv-row .cv-card .cv-label{font-size:large}.cv-home .cv-row .cv-card .cv-count{font-size:medium}.cv-home .cv-row .cv-card .cv-count .cv-delta{font-size:medium}.cv-home .cv-row .cv-card .cv-chart-container{height:60px}.cv-home .cv-row .cv-card:not(:first-child){margin-top:16px;margin-left:0}}@media screen and (max-width: 732px){.cv-home{padding:12px}}@-webkit-keyframes fade-in{to{opacity:1}}@keyframes fade-in{to{opacity:1}}

.select{margin:10px 0px}.select select{width:100%;height:40px;padding:10px;border:1px solid transparent;border-radius:4px}@media screen and (max-width: 480px){.select{margin:0px 0px 10px}}

